.footer{
    align-items: center;
    background-color: #414141;
    display: grid;
    grid-template-rows: auto  1fr auto;
    gap: 1rem;
    justify-content: stretch;
    padding: 3rem;
    width: 100%;
}

.logo-footer{
    display: flex;
    height: min(100px);
    justify-self: center;
}

.foot-items{
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
}

.foot-icons{
    display: flex;
    justify-content: center;
    gap: 2rem;
}

.foot-icon{
    color: white;
    font-size: 1.5rem;
    transition: 0.4s;
}

.foot-icon:hover{
    transform: scale(1.2);
}

.foot-links{
    display: flex;
    flex-direction: column;
    list-style: none;
    align-content: center;
    row-gap: 1rem ;
    column-gap: 1.5rem;
}

.footer-link{
    color: white;
    text-decoration: none;
}

.footer-link:hover{
    text-decoration:underline ;
}

.contact-items{
    display: grid;
    grid-template-rows: 1fr 1fr;
    gap: 1rem;
    justify-content: center;
}
.contact-item{
    color: white;
}

.contact-item h3{
    text-align: center;
    color: #414141;
    background-color: #F59C2F ;
    font-weight: 700;
}
.contact-item p{
    padding: 2px 0;
}

.icon-foot{
    position: relative;
    top: 3px;
    margin-right: 0.2rem;
}


@media screen and (max-width: 960px) {
    .footer {
        padding: 1rem;
        grid-template-rows: 1fr 1fr;
    }

    
}
